footer {
	height: 61px;
	position: fixed;
	width: 100%;
	bottom: 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-size: 12px;
	font-weight: normal;
	color: #858585;
	background-color: #fff;
	max-width: 1366px;
	font-family: "ArialMT";
}
footer a {
	color: #858585;
}
.footer__left {
	width: 404px;
	margin-left: 40px;
	display: flex;
	justify-content: space-between;
}
.footer__right {
	margin-right: 36px;
}